#footer{
    background-color: currentColor;
    width: 100%;
}

#footerContent{
    display: flex;
    flex-direction: row;
}

#footerSocialMedia{
    text-align: left;
    padding-left: 6%;
    padding-top: 2%;
}

#linkedIn{
    height: 65px;
}

#footerLink{
    flex: 1;
    height: 50px;
    width: 100%;
}

#footerLine{
    flex: 2;
    height: 100px;
    width: 10%;
    color: aqua;
    padding-left: 10%;
    padding-top: 4%;
}


/* for desktops */
@media only screen and (min-width: 768px) {
    #footerLine{
        flex: 2;
        height: 100px;
        width: 10%;
        color: aqua;
        padding-left: 40%;
        padding-top: 4%;
    }   
}   